:root {
  --foreground-rgb: 0, 0, 0;
  --background-rgb: 255, 255, 255;
}

body {
  color: black;
  background: white;
  margin: 0;
}

* {
  font-family: PingFang SC, Hiragino Sans GB, Microsoft YaHei,
    WenQuanYi Micro Hei, Helvetica Neue, Arial, sans-serif;
}
.marquee_box {
  position: absolute;
  top: 28px;
  left: 26px;
  width: 132px;
  height: 19px;
  overflow: hidden;
  word-break: keep-all;
  white-space: nowrap;
  display: flex;
  align-items: center;
}

.marquee_box p {
  display: inline-block;
  color: black;
  font-size: 12px;
  line-height: 19px;
}

.marquee_box:hover p {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
  cursor: default;
}
